<p>é¦–å…ˆï¼Œé‡åˆ°äº†ä¸€ä¸ªå¾ˆå‘çš„åœ°æ–¹ï¼Œé‚£å°±æ˜¯ä¸ç¡®å®šæ•°æ®åœ¨å“ªé‡Œã€‚ç½‘ä¸Šè¯´çš„é»˜è®¤åœ¨ <code>/var/lib/mysql</code>ï¼Œè™½ç„¶æˆ‘ä¹Ÿå‘ç°é‚£é‡Œæœ‰å¾ˆå¤šæ–‡ä»¶ï¼Œä¹Ÿæ˜¯ä¸æ•¢ç¡®å®šã€‚ç„¶ååˆæœ‰çš„è¯´å¯ä»¥åœ¨é…ç½®æ–‡ä»¶ <code>/etc/mysql/mysql.conf.d/mysqld.cnf</code> é‡Œé¢çœ‹ï¼Œç»“æœå¹¶æ²¡æœ‰è¿™ä¸ªæ–‡ä»¶ï¼Œä¹Ÿè®¸æ˜¯å› ä¸ºæˆ‘æ˜¯ MariaDBï¼Œæ‰€ä»¥åº”è¯¥åœ¨ <code>/etc/mysql/my.cnf</code> é‡Œé¢ã€‚ç¡®å®æœ‰è¿™ä¸ªæ–‡ä»¶ï¼Œä½†æ˜¯é‡Œé¢å¹¶æ²¡æœ‰ <code>datadir</code> è¿™ä¸ªå‚æ•°ï¼ŒçœŸæ˜¯é†‰äº†ã€‚åˆ°åæ¥æˆ‘é¼“èµ·å‹‡æ°”æŠŠ <code>datadir</code> è®¾ç½®æˆäº† <code>/mysql-db</code>ï¼Œå¼€å§‹äº†è¿ç§»ä¹‹è·¯ã€‚</p>
